<template>
  <div>
    <!-- ДИАЛОГИ -->
    <dialog-accept-cards-rules v-if="dialogs.acceptCardsRules" />
    <div v-if="!dialogs.acceptCardsRules">
      <v-alert
        class="ma-2"
        text
        hide-details
        :color="profile.bank_cards_rules_accepted === 0 ? 'red' : 'green'"
      >
        <div v-if="profile.bank_cards_rules_accepted === 0">
          <span v-if="profile.language === 'en'">
            You have not accepted
            <span
              class="span-link"
              @click="openBankCardsRules(false)"
            >
              {{ $t('bankCards.dialogs.bankCardsSection') }}
            </span>
            rules yet
          </span>
          <span v-if="profile.language === 'ru'">
            Вы не приняли правила пользования
            <span
              class="span-link"
              @click="openBankCardsRules(false)"
            >
              {{ $t('bankCards.dialogs.bankCardsSection') }}
            </span>
          </span>
        </div>
        <div v-if="profile.bank_cards_rules_accepted !== 0">
          <span v-if="profile.language === 'en'">
            You have accepted
            <span
              class="span-link"
              @click="openBankCardsRules(true)"
            >
              {{ $t('bankCards.dialogs.bankCardsSection') }}
            </span>
            rules yet
          </span>
          <span v-if="profile.language === 'ru'">
            Вы приняли правила пользования
            <span
              class="span-link"
              @click="openBankCardsRules(true)"
            >
              {{ $t('bankCards.dialogs.bankCardsSection') }}
            </span>
          </span>
        </div>
      </v-alert>
    </div>
  </div>
</template>
<script>
import DialogAcceptCardsRules        from '@/components/bankCards/bankCardsTab/BankCardsTabDialogAcceptCardsRules';
import {mapGetters}                  from 'vuex';

export default {
  name: 'BankCards',

  components: {
    DialogAcceptCardsRules
  },

  computed: {
    ...mapGetters({
      dialogs: 'bank_cards/dialogs',
      profile: 'main/profile'
    })
  },

  async created() {
    await this.$store.dispatch('main/loadProfile');
    if (this.profile.bank_cards_rules_accepted === 0) {
      this.$store.dispatch('bank_cards/openDialog', 'acceptCardsRules');
    } 
  },

  methods: {
    async openBankCardsRules(status) {
      await this.$store.dispatch('bank_cards/setRulesAccepted', status);
      this.$store.dispatch('bank_cards/openDialog', 'acceptCardsRules');
    }
  }
};
</script>