<template>
  <v-tabs
    centered
    grow
    :value="tabNumber"
    :height="32"
  >
    <!-- ПРАВИЛА FACEBOOK -->
    <v-tab
      key="tab-bank-cards"
      :value="0"
      @click="changeTab(0)"
    >
      {{ $t('bankCards.tabs.bankCards') }}
    </v-tab>

    <!-- ГРУППЫ ПРАВИЛ -->
    <v-tab 
      key="tab-analytics"
      :value="1"
      @click="changeTab(1)"
    >
      {{ $t('bankCards.tabs.analytics') }}
    </v-tab>
  </v-tabs>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'BankCardsTabs',

  data: () => ({
    tabs: ['bank-cards-tab', 'analytics'],
  }),

  computed: {
    ...mapGetters({
      activeTab: 'bank_cards/activeTab',
    }),

    tabNumber() {
      if (typeof this.$route.params.tab === 'undefined' || this.$route.params.tab === 'bank-cards-tab') {
        return 0;
      } else if (this.$route.params.tab === 'analytics') {
        return 1; 
      }
      return 0;
    }
  },

  methods: {
    changeTab(tab) {
      this.$store.dispatch('bank_cards/setActiveTab', {
        tab: tab,
        component: this.tabs[tab]
      });
    }
  }
};
</script>