<template>
  <v-dialog
    :value="dialogs.acceptCardsRules"
    persistent
    scrollable
    max-width="800px"
  >
    <v-card style="max-height: 270px;">
      <v-card-title>
        <span class="headline">
          {{ $t('bankCards.dialogs.acceptBankCardsPageRulesHeadline') }}
        </span>
      </v-card-title>

      <v-card-text>
        <span v-html="$t('bankCards.dialogs.acceptBankCardsPageRulesText')" />
        <v-checkbox
          v-if="!rulesAccepted"
          v-model="acceptRulesCheckbox"
          :label="$t('bankCards.dialogs.acceptBankRules')"
        />
      </v-card-text>

      <v-card-actions>
        <v-spacer />
        <v-btn
          v-if="!rulesAccepted"
          color="blue darken-1"
          text
          :disabled="!acceptRulesCheckbox"
          @click="acceptRules"
        >
          {{ $t('bankCards.dialogs.accept') }}
        </v-btn>
        <v-btn
          v-if="rulesAccepted"
          color="blue darken-1"
          text
          @click="$store.dispatch('bank_cards/closeDialog', 'acceptCardsRules')"
        >
          {{ $t('common.close') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'BankCardsTabDialogAcceptCardsRules',

  data() {
    return {
      acceptRulesCheckbox: false
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'bank_cards/dialogs',
      rulesAccepted: 'bank_cards/rulesAccepted'
    })
  },

  methods: {
    async acceptRules() {
      await this.$store.dispatch('bank_cards/acceptBankCardsRules');
      await this.$store.dispatch('main/loadProfile');
    }
  }
};
</script>